import React, { useState, useEffect } from "react";
import Header from "../../components/header/header";
import styles from "./devices.module.css";
import deviceProvider, { DevicesType } from "../../providers/deviceProvider";
import house from "./house.png";
import doorOpen from "./door_open.png";
import doorClose from "./door_close.png";
import motion from "./motion.png";
import {
  toSnakeCase,
  toTitleCase,
  removeUnderscores
} from "../../utils/stringFormatting";
import AlarmSettings from "../../components/alarmSettings/alarmSettings";

const formatDeviceId = (str: string) =>
  toTitleCase(removeUnderscores(toSnakeCase(str)));

const Devices: React.FC = () => {
  const [devices, setDevices] = useState(null as DevicesType | null);
  const [deviceLocations, setDeviceLocations] = useState(null as any);

  if (!devices) deviceProvider.getAll().then(setDevices);
  if (!deviceLocations) {
    deviceProvider.update();
    deviceProvider.getLocations().then(setDeviceLocations);
  }

  useEffect(() => {
    let timeout: number = 0;
    function getLatestDeviceStatus() {
      deviceProvider.getAll().then(setDevices);
      clearTimeout(timeout);
      timeout = window.setTimeout(getLatestDeviceStatus, 5000);
    }
    timeout = window.setTimeout(getLatestDeviceStatus, 5000);
    return function cleanup() {
      clearTimeout(timeout);
    };
  });

  const setValue = (id: keyof DevicesType, status: boolean) => {
    if (!devices) return;
    deviceProvider.put(id, status);
    setDevices({ ...devices, [id]: status });
  };

  let peopleCount = 0;

  return (
    <React.Fragment>
      <Header />
      <div className={styles.container}>
        <AlarmSettings />
        <div className={styles.diagram}>
          <img className={styles.img} src={house} alt="home" />
          {Object.keys(devices || {}).map(id => {
            if (!devices) return null;
            if (!deviceLocations) return null;

            const deviceId = id as keyof DevicesType;
            const device = deviceLocations[deviceId];
            const status = devices[deviceId];
            if (!device) return null;

            if (device.type === "person") {
              peopleCount++;
              return (
                <div
                  key={deviceId}
                  className={styles.people}
                  style={{
                    left: 280,
                    top: 20 + peopleCount * 20,
                    opacity: status !== "away" ? 1 : 0.3
                  }}
                >
                  {formatDeviceId(deviceId)}
                </div>
              );
            }

            if (device.type === "light") {
              return (
                <div
                  key={deviceId}
                  className={styles.device}
                  style={{
                    left: device.x,
                    top: device.y,
                    opacity: status ? 1 : 0.3
                  }}
                  onClick={() => setValue(deviceId, !status)}
                >
                  <span role="img" aria-label="light">
                    💡
                  </span>
                  <div className={styles.tooltip}>
                    {formatDeviceId(deviceId)}
                  </div>
                </div>
              );
            }
            if (device.type === "speaker") {
              return (
                <div
                  key={deviceId}
                  className={styles.device}
                  style={{
                    left: device.x,
                    top: device.y,
                    opacity: status ? 1 : 0.3
                  }}
                  onClick={() => setValue(deviceId, !status)}
                >
                  <span role="img" aria-label="speaker">
                    🔊
                  </span>
                  <div className={styles.tooltip}>
                    {formatDeviceId(deviceId)}
                  </div>
                </div>
              );
            }
            if (device.type === "siren") {
              return (
                <div
                  key={deviceId}
                  className={styles.device}
                  style={{
                    left: device.x,
                    top: device.y,
                    opacity: status ? 1 : 0.3
                  }}
                  onClick={() => setValue(deviceId, !status)}
                >
                  <span role="img" aria-label="siren">
                    🚨
                  </span>
                  <div className={styles.tooltip}>
                    {formatDeviceId(deviceId)}
                  </div>
                </div>
              );
            }
            if (device.type === "door") {
              return (
                <div
                  key={deviceId}
                  className={styles.device}
                  style={{
                    left: device.x,
                    top: device.y,
                    opacity: status ? 1 : 0.3
                  }}
                >
                  {status ? (
                    <img src={doorOpen} alt="door open" />
                  ) : (
                    <img src={doorClose} alt="door close" />
                  )}
                  <div className={styles.tooltip}>
                    {formatDeviceId(deviceId)}
                  </div>
                </div>
              );
            }
            if (device.type === "motion") {
              return (
                <div
                  key={deviceId}
                  className={styles.device}
                  style={{
                    left: device.x,
                    top: device.y,
                    opacity: status ? 1 : 0.3
                  }}
                >
                  <img src={motion} alt="motion" />
                  <div className={styles.tooltip}>
                    {formatDeviceId(deviceId)}
                  </div>
                </div>
              );
            }
            return null;
          })}
          <div />
        </div>
      </div>
    </React.Fragment>
  );
};

export default Devices;
